ks-input-select {
    display: block;
    margin-bottom: 15px;
}

ks-input-select label {
    display: block;
    margin-bottom: 3px;

    color: #000000;
    font-size: 14px;
}

ks-input-select[error] label {
    color: var(--color-secondary-darker);
}


ks-input-select .wrapper {
    position: relative;
}

ks-input-select ks-icon {
    position: absolute;
    right: 10px;
    top: 8px;
    color: #4b4b4b;
}

ks-input-select select {
    height: 40px;
    vertical-align: middle;
    display: inline-block;
    appearance: none;

    max-width: 100%;
    width: 100%;
    padding: 0 10px;
    background: #f4f4f4;
    color: #373737;
    border: 1px solid #e5e5e5;
    overflow: visible;

    box-sizing: border-box;
    border-radius: 0;
    font: inherit;
}

ks-input-select select:focus {
    outline: none;
    background-color: #f4f4f4;
    color: #373737;
    border-color: #222222;
}

ks-input-select[error] select {
    border-color: var(--color-secondary-darker);
    color: var(--color-secondary-darker);
}

ks-input-select[error] ks-icon {
    color: var(--color-secondary-darker);
}


ks-input-select[lightUp] select {
    border-color: #1488ff;
    background-color: #d5e6ff;
    color: #004454;
}

ks-input-select[lightUp] ks-icon {
    color: #004454;
}
