import { Component, h, Prop, Method } from '@stencil/core';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-select',
      styleUrl: 'inputSelect.css'
})
export class InputSelect implements InputInterface {

    @Prop() name: string;
    @Prop() label: string;

    @Prop({mutable: true, reflect: true}) error: boolean;
    @Prop({mutable: true, reflect: true}) lightUp: boolean = false;

	render() {
		return [
            this.label ?
                <label>{this.label}</label>
            : null,

            <div class="wrapper">
                <select name={this.name} onChange={()=>this.Validate()}>
                    <slot/>
                </select>

                <ks-icon name="chevron-down"/>
            </div>
        ];
    }

    @Method()
    async IsValid() {
        return true;
    }

    @Method()
    async Validate() {
        this.lightUp = false;
        return Promise.resolve();
    }
}
